import React, { useEffect, useState } from "react";

const MediaRenderer = ({ url, onClick, className }) => {
  const [isImage, setIsImage] = useState(null); // null = loading, true = image, false = video
  const [isIphone, setIsIphone] = useState(false);

  // Detect if device is an iPhone
  useEffect(() => {
    const userAgent = window.navigator.userAgent || "";
    if (/iPhone|iPad|iPod/i.test(userAgent)) {
      setIsIphone(true);
    }
  }, []);

  useEffect(() => {
    if (!url) return;

    const img = new Image();
    const isAvif = url.endsWith(".avif");
    const imgUrl = isAvif && isIphone ? url.replace(".avif", ".jpg") : url;

    img.onload = () => setIsImage(true);
    img.onerror = () => setIsImage(false);
    img.src = imgUrl;
  }, [url, isIphone]);

  if (isImage === null) return <p></p>;

  return (
    <>
      {isImage ? (
        isIphone ? (
          <img
            src={url.endsWith(".avif") ? url.replace(".avif", ".jpg") : url}
            alt="media"
            className={className}
            style={{ maxWidth: "100%" }}
            onClick={onClick}
          />
        ) : (
          <picture>
            {url.endsWith(".avif") && <source srcSet={url} type="image/avif" />}
            <img
              src={url.endsWith(".avif") ? url.replace(".avif", ".jpg") : url}
              alt="media"
              className={className}
              style={{ maxWidth: "100%" }}
              onClick={onClick}
            />
          </picture>
        )
      ) : (
        <video controls style={{ maxWidth: "100%" }} onClick={onClick}>
          <source src={url} />
          Your browser does not support the video tag.
        </video>
      )}
    </>
  );
};

export default MediaRenderer;
